<template>
   <article class="flex flex-col md:flex-row w-11/12 md:w-9/12 border border-white bg-white">      
      <figure class="w-72">         
         <img :src="item.image" alt="" class="bg-cover"/>
      </figure>
      
      <section class="flex flex-col justify-between p-5 gap-4 md:gap-0 w-full md:w-8/12">

         <div class="flex flex-row justify-between">
            <!-- Parte Izq -->
            <div class="col-span-1 gap-5 justify-between">
               <!-- Title -->
               <div class="flex flex-col text-left">
                  <span class="text-blue_1 uppercase font-DIN-Bold text-xl text-left" v-text="item.car_category.alias"></span>
                  <span class="text-light_gray_1 text-left uppercase text-sm">CATEGORÍA</span>
               </div>                                       
   
            </div>
   
            <!-- Parte Derecha -->
            <div class="grid col-span-1 gap-10 md:gap-5 md:mb-5">
               <div class="flex flex-col">
                  <span class="text-blue_1 uppercase font-DIN-Bold text-sm text-right" v-text="item.car_engine.alias"></span>
                  <span class="text-light_gray_1 uppercase font-DIN-Regular text-sm text-right">TRANSMISIÓN</span>
               </div>
            </div>
         </div>

         <!-- Precio de fee de combustible -->
         <small class="text-sky-500 self-end" v-if="item.car_category.fuel > 0">
            <i class="fa fa-info-circle"></i>
            Tarifa adicional de combustible <b class="text-xl">${{ item.car_category.fuel }}</b>
         </small>
         
         <!-- Form -->
         <div class="flex flex-col md:flex-row gap-5 mt-5 items-end md:justify-end" v-if="!search">            
               
               <!-- Date -->
               <div class="flex bg-white text-gray-700 w-64">
                  <Datepicker 
                     class="datepicker-container"
                     placeholder="Fecha de recogida y entrega"
                     v-model="car.dateRange"
                     :min-date="new Date()"
                     :format="'dd/MM/yyyy'"
                     range multi-calendars 
                     auto-apply
                     :enable-time-picker="false"
                     :close-on-auto-apply="true"
                     @update:modelValue="handleDate"
                  />
               </div>
               <button class="bg-orange_1 py-2 font-DIN-Bold w-36 border hover:bg-white hover:text-orange_1 hover:border-orange_1" @click="getPrice()">Buscar</button>
         </div>

      </section>

      <!-- Precio -->
      <section class="border border-gray-200 flex flex-col md:w-1/4 justify-center items-center gap-5 py-5">
         <div class="flex flex-col">
            <i class="fa fa-spinner fa-pulse text-gray-500" v-show="loadingPrices"></i>
            <span class="text-orange_1 font-DIN-Bold text-3xl" v-show="!loadingPrices"><i class="fa fa-usd"></i>{{ price }}</span>
            <span class="text-light_gray_1" v-show="this.isPriceTextoByDay && !this.search">Por día</span>
            <span class="text-light_gray_1" v-show="!this.isPriceTextoByDay|| this.search">Precio total</span>      
         </div>
         <button
            :class="{'btn-disbled':btnDisabled}"
            class="bg-white py-2 font-DIN-Bold w-36 border hover:bg-dark_blue_1 hover:text-white text-dark_blue_1  border-dark_blue_1" 
            :disabled="btnDisabled"
            @click="goRequest()">Enviar Solicitud</button>
      </section>

   </article>
</template>
<script>
import moment from 'moment';
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';

import Authorization from "@/services/user/auth.service";
import {useValidationDate} from '@/composables/useValidationDate';


export default {
   name: 'CarListTemplate',   
   props: {
      item: [],
      index: {
         type: Number
      },
      not_found: {
         type: Boolean,
         default: false
      },
      params: {},
      search: {
         type: Boolean,
         default: false
      }
   },
   components: {
      Datepicker
   },
   data() {
      return {
         // results: [],
         price: 0,
         priceWFuel: 0,
         pricePerDay: 0,
         loadingPrices: false,      // ico loading cuando se busca en el api
         isSelected: false,         // Si escoge fecha 
         isPriceTextoByDay: true,  // Si devuelven precio se pone precio x dia y si hay precio total
         btnDisabled: false,        // Si se consulta el API y devuelve un precio 
         car:{
            dateRange: {},
            dateString: null,
            dates:''
         },
         select_style_opt: {
            container: 'relative mx-auto w-full flex items-center justify-end box-border cursor-pointer border border-blue_1 bg-white text-base leading-snug outline-none',
            search: 'w-full absolute inset-0 outline-none focus:ring-0 appearance-none box-border border-8 border-transparent text-base font-sans bg-white pl-2 rtl:pl-0 rtl:pr-3.5',                                                        
            placeholder: 'flex items-center w-full h-full absolute left-0 top-0 pointer-events-none bg-transparent leading-snug pl-3.5 text-gray-400 rtl:left-auto rtl:right-0 rtl:pl-0 rtl:pr-3.5',
            optionSelected: 'text-gray-700 bg-light_gray_1'
         },
      }
   },
   created(){
      this.updPrice();
      if(!this.search) return
      const init_params = JSON.parse(localStorage.getItem('searchform'));
      if(init_params) {
            if(init_params.type === 'car'){
               this.handleDate([init_params.date_start, init_params.date_out])
            }
         }
   },
   methods: {
      formatDate() {         
         return {
            from: moment(this.car.dates.from, 'DD-MM-YYYY').format('YYYY-MM-DD'),
            to: moment(this.car.dates.to, 'DD-MM-YYYY').format('YYYY-MM-DD'),
         }
      },
      goRequest() {
         
         //Elimino posible informacion residual
         localStorage.removeItem('request_info')
         
         // No hay fecha y no es por resultado de busqueda
         if(!this.search){            
            const isDateOk = useValidationDate([this.car.dates.from,this.car.dates.to]);
            if(!isDateOk) return;

            if(!this.isSelected)
               return this.showErrorFormValidation('Debe pulsar el boton buscar')
         }
         /* if(!isDateOk && !this.search)
            return this.showErrorFormValidation(); */

         // No se ha seleccionado fecha y no es resultado de busqueda
         /* if(!this.isSelected && !this.search)
            return this.showErrorFormValidation('Debe pulsar el boton buscar') */
         
         // Si el producto no tiene precio asociado
         if(this.btnDisabled)
            return this.showErrorFormValidation('El producto no tiene precio asociado, no puede ser seleccionado')
         else{
               
               this.isSelected = false;               
               const request = {
                  type: 'car',
                  destination: 'Cuba',
                  country: 'Cuba',
                  pole: 'Cuba',
                  product: 'Car',
                  product_id: this.item.id,
                  product_name: this.item.alias,
                  initprice: this.item.price_from || 0,  
                  is_home_request: false,
                  prod:[],              
                  price: this.price,      
                  priceWFuel: this.priceWFuel,
                  date: this.car.dates.from + ' / ' + this.car.dates.to,
                  car_category: this.item.car_category,
                  car_engine: this.item.car_engine,
               }
               request.prod.push({
                  adults: 1,
                  price: this.pricePerDay,
                  price_adult: this.pricePerDay,                  
                  product_id: this.item.car_category.id,
                  type: this.item.car_category.alias,
                  date_start: this.formatDate().from,
                  date_out: this.formatDate().to,
               })
               
               localStorage.setItem('request_info', JSON.stringify(request));
               this.$router.push({ name: 'SendRequest' });

            }      

      },

      async calcPrice(){
         
         this.loadingPrices = true;         
         const params = {}
         params.date_from = this.formatDate().from
         params.date_to = this.formatDate().to
         params.car_category_id = this.item.car_category.id
         params.car_engine_id = this.item.car_engine.id
         params.auth = 'bearer'
         let price_from = this.price
         
         const prices = await Authorization.checkLogin('/search/car/prices', params, 'post')
         .then(el => {

            if(el.data){
               if(el.data.length !== 0){
                  this.btnDisabled = false
                  this.isPriceTextoByDay = false
                  return el.data
               }
               else{
                  this.btnDisabled = true
                  this.isPriceTextoByDay = true
                  this.price = price_from
                  this.$swal({
                     title: 'Información',
                     text: `Esta categoria de autos no tiene precio para el rango de fechas ${moment(this.car.dates.from, 'DD/MM/YYYY').format('DD/MM/YYYY')} - ${moment(this.car.dates.to, 'DD/MM/YYYY').format('DD/MM/YYYY')}, intente con otro`,
                     icon: 'info',
                     confirmButtonColor: '#3085d6',                            
                  }) 
               }
               
            }
            else{
               this.btnDisabled = true
               this.price = price_from               
               throw 'Error'
            }
         })
         .catch(error => {
               this.$swal({
               title: 'Error',
               text: error||'Ocurrio un error!!!',
               icon: 'error',                
               confirmButtonColor: '#3085d6',                            
            }) 
         })

         if(prices){
            this.price = prices[0].totalPrice 
            this.priceWFuel = prices[0].price 
            this.pricePerDay = prices[0].pricePerDay
         } else {
            this.price = price_from 
            this.priceWFuel = 0
         }
         
         this.loadingPrices = false;
      },

      getPrice() {
         const isDateOk = useValidationDate(this.car.dateRange);
         if(isDateOk){
            this.isSelected = true;
            this.calcPrice()
         }

      },

      handleProductFilter(list){
         this.product_list = list
      },
      
      handleDate(range_date) {

            this.car.dateRange = {}
            if(!range_date) return
            this.car.dateRange = range_date
            this.car.dates = {
               from: moment(range_date[0]).format('DD-MM-YYYY'),
               to: moment(range_date[1]).format('DD-MM-YYYY'),
            }            
      },

      updPrice(){
         // Se selecciona producto en la lista
         if(this.price) return this.price

         // Si es un resultado de busqueda
         if(this.item.totalPrice) {
            this.priceWFuel = this.price;            
            return this.price = this.item.totalPrice
         }

         // Si no es resultado de busqueda y entro x primera vez
         return this.price = this.item.price_from || 0;
      },

      showError(text) {
         text = text || "Ocurrio un error!";
         this.$swal({
            title: 'Error',
            text: text,
            icon: 'error',                
            confirmButtonColor: '#3085d6',                            
         })            
      },
      
      showErrorFormValidation(text) {
         text = text || "Debe seleccionar las opciones requeridas antes de seleccionar un auto!";
         this.$swal({
            title: 'Validación de formulario',
            text: text,
            icon: 'warning',                
            confirmButtonColor: '#3085d6',                            
         })            
      },
   }
}
</script>
<style scoped>
.btn-disbled{
   @apply bg-light_gray_1 text-white border-light_gray_1
}

.img-prod {
   width: 380px;
   height: 250px;
}

/deep/ .dp__input{
   border-radius: 0px;
   border: none;
   background-color: white;
}

/deep/ .dp__input_wrap input{   
   height: 2.5rem;
}
</style>
